@charset "UTF-8";
:root {
  --clip: left center;
  --size: 0;
  --background-thumb: #2b3769;
  --scrollbar-thumb-bgcolor: #757575;
  --stripe-width: 10px;
  --nav-marker-border: #3b8686;
  --sidebar-width: 28.5rem;
  --duration: 0.3s;
  --button-size: 4rem;
  --mob-expanded-width: 320px;
  --mob-collapsed-width: 4rem;
}

.masked-input {
  color: transparent; /* Скрыть текст, пока маска не активна */
}

.masked-input::placeholder {
  color: black; /* Цвет плейсхолдера */
}

.gradient-preloader {
  background: linear-gradient(90deg, #2b3769 -23.739%, #93a9ff 96.778%);
}

@media (min-width: 1440.99px) {
  .underline:hover {
    font-weight: 600;
    opacity: 1;
  }
  .underline:hover::before {
    width: 47%;
  }
  .underline:hover::after {
    width: 47%;
  }
  .underline::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0px;
    width: 0;
    height: 2px;
    background-color: #000;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0px;
    width: 0;
    height: 2px;
    background-color: #000;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline-w:hover {
    font-weight: 600;
    opacity: 1;
  }
  .underline-w:hover::before {
    width: 47%;
  }
  .underline-w:hover::after {
    width: 47%;
  }
  .underline-w::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0px;
    width: 0;
    height: 2px;
    background-color: #fff;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline-w::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0px;
    width: 0;
    height: 2px;
    background-color: #fff;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
}
.answers__title {
  padding-bottom: 70px;
  color: rgb(7, 7, 7);
  text-align: center;
  border-bottom: 1px solid #a9a9a9;
}
@media screen and (max-width: 690px) {
  .answers__title {
    padding-bottom: 24px;
  }
}
@media screen and (max-width: 320px) {
  .answers__title {
    font-size: 24px;
  }
}
.answers__content {
  border-bottom: 1px solid rgb(169, 169, 169);
}
.answers__content._open ._icon {
  transform: rotate(-405deg);
}
.answers__question {
  padding: 20px 0 20px;
  user-select: none;
  cursor: pointer;
  display: flex;
  gap: 8px;
  align-items: center;
  justify-content: space-between;
}
.answers__question ._icon {
  font-size: 20px;
  transition: transform 0.4s ease-in-out;
  color: #2b3769;
}
.answers__question ._icon::before {
  font-weight: 700;
}
.answers__icon {
  display: flex;
  align-items: center;
}
.answers__text {
  pointer-events: none;
  font-size: 30px;
  font-weight: 400;
  line-height: 123.333333%; /* 37px/30 */
}
.answers__text span {
  white-space: nowrap;
}
@media (min-width: 320.98px) and (max-width: 1920px) {
  .answers__text {
    font-size: calc(15px + 15 * (100vw - 320px) / 1600);
  }
}
@media screen and (max-width: 320px) {
  .answers__text {
    font-size: 15px;
  }
}
.answers__wrapper {
  min-height: 0;
}
.answers__answer {
  padding: 5px 0 40px;
  min-height: 0;
}